#contentWishList-div {
  display: flex;
  flex-direction: column;
  background-color: yellowgreen;
  width: 95%;
}

.list-div {
  display: flex;
  flex-direction: column;
  background-color: aqua;
  text-align: center;
  border-radius: 20px;
  margin: 10px 0;
  cursor: pointer;
  align-items: center;
}

.listHeder-div {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.listHeder-button {
  border-radius: 30%;
  color: blue;
  margin-right: 10px;
}

#formNewList-div {
  display: flex;
  justify-content: center;
}

.listItems-div {
  background-color: violet;
  width: 80%;
}
